import { Component, OnInit, OnChanges } from '@angular/core';
import { Question } from '../../model/question';
import { DataService } from '../data.service';
import { Answer } from '../../model/answer';
@Component({
  selector: 'app-question-list',
  templateUrl: './question-list.component.html',
  styleUrls: ['./question-list.component.css']
})
export class QuestionListComponent implements OnInit {

  private questions: Question[] = [Question.emptyQuestion()];
  private index = 0;
  private isQuiz = true;
  private properAnswers = 0;

  constructor(private dataService: DataService) { }

  getIndex(): number {
    return this.index;
  }

  ngOnInit() {
    this.questions = this.dataService.getQuestions();
    this.shuffle(this.questions);
  }

  shuffle(array) {
    let currentIndex = array.length, temporaryValue, randomIndex;
    // While there remain elements to shuffle...
    while (0 !== currentIndex) {
      // Pick a remaining element...
      randomIndex = Math.floor(Math.random() * currentIndex);
      currentIndex -= 1;
      // And swap it with the current element.
      temporaryValue = array[currentIndex];
      array[currentIndex] = array[randomIndex];
      array[randomIndex] = temporaryValue;
    }
    return array;
  }

  prev() {
    if (this.index === 0) {
      this.index = this.questions.length;
    }
    this.index--;
  }

  next() {
    this.index++;
    this.index = this.index % this.questions.length;
  }

  submit() {
    this.isQuiz = false;
    this.properAnswers = this.dataService.countProperAnswers(this.questions);
    console.log(JSON.stringify(this.questions));
  }

  reset() {
    this.properAnswers = 0;
    this.isQuiz = true;
    for (const question of this.questions) {
      question.answers = [];
    }
  }
}
