import { Component, EventEmitter, Input, OnInit, Output, OnChanges } from '@angular/core';
import { Question } from './../../model/question';
import { Answer } from '../../model/answer';
import { DataService } from '../data.service';
@Component({
  selector: 'app-question',
  templateUrl: './question.component.html',
  styleUrls: ['./question.component.css']
})
export class QuestionComponent implements OnInit, OnChanges {

  @Input()
  private question: Question;

  private answers: Answer[];

  constructor(private dataService: DataService) { }

  ngOnInit() {
    this.answers = this.dataService.getAnswers(this.question.id);
  }

  ngOnChanges() {
    this.answers = this.dataService.getAnswers(this.question.id);
  }

  setAnswer(answer) {
    if (this.question.answers.includes(answer.id)) {
      const index = this.question.answers.indexOf(answer.id, 0);
      if (index > -1) {
        this.question.answers.splice(index, 1);
      }
    } else {
      this.question.answers.push(answer.id);
    }

    this.question.answers.forEach((item) => {
      console.log(item);
    });
  }

  isChoosen(answer): boolean {
    return this.question.answers.includes(answer.id);
  }
}
