import { Injectable } from '@angular/core';
import { Answer } from '../model/answer';
import { Question } from '../model/question';

@Injectable({
  providedIn: 'root'
})
export class DataService {

  constructor() { }

  getQuestions(): Question[] {
    return [new Question( 1, 'Upo≈õledzona tolerancja glukozy oznacza:'), new Question( 2, 'What is B?'), new Question( 3, 'What is C?')];
  }

  getAnswers(questionId: number): Answer[] {
    if (questionId === 1) {
      return [{'id': 1, 'value': 'This is caused by A'}, {'id': 2, 'value': 'This is caused by B'},
      {'id': 3, 'value': 'This is caused by C'}];
    } else if (questionId === 2) {
      return [{'id': 4, 'value': 'This is caused by D'}, {'id': 5, 'value': 'This is caused by E'},
      {'id': 6, 'value': 'This is caused by F'}];
    }
    return [{'id': 7, 'value': 'This is caused by G'}, {'id': 8, 'value': 'This is caused by H'}];
  }

  countProperAnswers(questions: Question[]) {
    let count = 0;
    for (const question of questions) {
      for (const i of question.answers) {
        if (i % 2 === 0) {
          count++;
        }
      }
    }
    return count;
  }
}
